const backGroundColorsArea = {
  'process control and instrumentation': 'darkred',
  // 'process integration and intensification': 'orange',
  'process modelling and simulation': 'darkgreen',
  'industrial applications of pse tools': 'blue',
  'fault detection and diagnostics': '#a52a2a',
  'process synthesis and design': '	#800000',
  'process integration and intensification': 'darkgrey',
  // 'modelagem molecular e projeto de produto': 'brown',
  'data mining and machine learning': 'black',
  'process optimization': '#3cb371',
  'molecular modelling and product design': '#2f4f4f',
  'production planning and scheduling': '#808000',
}

/**
 *
 * @type {{docResource: String, docTitle: String, imgTitle: String, ratingCount: String, ratingValue: String, summaryText: String}}
 */

function List({ ID, TITLE, AUTHOR, TYPE, LOCATION,
  DATE, ABSLINK, FORMLINK, AXIS, PAPERLINK,
  favorited, handleFavoriteButton }) {
  const classes = useStyles()
  const work = {
    ID, TITLE, AUTHOR, TYPE, LOCATION,
    DATE, ABSLINK, FORMLINK, AXIS, PAPERLINK,
    favorited
  }
  // console.log('List', ID)
  return (
    <Card className={classes.card}>
      <div className={classes.details}>
        <section className={classes.cardHeader} style={{ backgroundColor: backGroundColorsArea[AXIS.toLowerCase()] }}>
          {AXIS}
        </section>

        <CardContent className={classes.content}>
          <Grid container >
            <Grid item xs={10} container direction="column">
              {/* <ButtonBase
              target="_blank"
              rel="noreferrer"
              href={ABSLINK}
            > */}
              <CardActionArea
                target="_blank"
                rel="noreferrer"
                href={ABSLINK}
              >
                <Typography variant="caption" className={classes.bold}>
                  {TITLE}
                </Typography>
                <Typography variant="body2">
                  {AUTHOR}
                </Typography>
                <section className={classes.smallInfos}>
                  <Typography variant="caption" color="textSecondary" className={classes.infosItens}>
                    ID: {ID}
                  </Typography>
                  <Typography variant="caption" color="textSecondary" className={classes.infosItens}>
                    Type: {TYPE}
                  </Typography>
                </section>
                <section className={classes.summary}>
                  <Typography variant="caption" color="textSecondary" className={classes.infosItens}>
                    Location: {LOCATION}
                  </Typography>
                </section>
              </CardActionArea>
            </Grid>
            <Grid container item xs={2} direction="column">
              { TYPE === 'Poster' ?
                (
                <Tooltip title="Evaluate" aria-label="Evaluate">
                  <IconButton tooltip="Evaluate" style={{ padding: 2 }}
                    onClick={(e) => handleFavoriteButton(e, work)}>
                    <Grade
                      color={favorited === true ? 'secondary' : 'inherit'}
                    >
                    </Grade>
                  </IconButton>
                </Tooltip>)
              : null
            }
              {/* <Tooltip title="Rate" aria-label="Rate">
                <IconButton tooltip="Rate" style={{ padding: 2 }}
                  target="_blank"
                  href={FORMLINK}
                  onClick={handleAvaliarClick}>
                  <InsertChartOutlined></InsertChartOutlined>
                </IconButton>
              </Tooltip> */}
              <Tooltip title="Open Abstract" aria-label="Open Abstract">
                <IconButton tooltip="Open Abstract" style={{ padding: 2 }}
                  target="_blank"
                  rel="noreferrer"
                  href={ABSLINK}
                  onClick={handleVisualizarClick}>
                  <AttachmentOutlined></AttachmentOutlined>
                </IconButton>
              </Tooltip>
            </Grid>
          </Grid>


        </CardContent>
      </div>
    </Card>
  )
}

function componentIsEqual(prevProps, nextProps) {
  if (prevProps.ID === nextProps.ID &&
    prevProps.favorited === nextProps.favorited
  ) {
    return true
  }
  return false
}

export default React.memo(List, componentIsEqual)
